import React, { useEffect, useState } from 'react';
import { Grid, Typography } from '@material-ui/core';
import { BaseScreen } from "../components";
import { Zoom, Fade, Roll } from 'react-reveal';
import '../App.css';

export default ({ history, match }) => {
    let [state, setState] = useState({});
    let [imgs, setImgs] = useState([]);

    useEffect(_ => {
        let state = require('../assets/projects.json').find(value => value.id == match.params.id);
        if (typeof state == 'undefined') {
            history.replace('../Portifolio')
        } else {
            setState(state)
            setImgs(state.images)
            document.title = state.title;
        }
    }, [])


    return (
        <BaseScreen history={history}>
            <Grid container>
                <Grid item sm={12} xs={12}>
                    <Fade up>
                        <Roll>
                            <h1 className='nomeC'>{state.title}</h1>
                        </Roll>
                        <Zoom >
                            <Typography className='nomeC'>
                                {state.description}
                            </Typography>
                        </Zoom>
                    </Fade>
                </Grid>
                {
                    imgs.map((img, index) =>
                        <Grid item sm={12} xs={12}>
                            <Fade>
                                <img alt={'imagem' + index} style={{ width: '65%', margin: 10 }} src={require('../assets/' + state.image + img)}></img>
                            </Fade>
                        </Grid>
                    )
                }

            </Grid>
        </BaseScreen>
    )
}

