import React, { useEffect } from 'react';
import '../App.css';
import { BaseScreen } from "../components";
import Grid from '@material-ui/core/Grid';

export default ({ history }) => {

    useEffect(_ => { document.title = "Sobre" }, [])
    return (
        <BaseScreen history={history}>
            <Grid container
                style={{
                    backgroundImage: `url(${'../assets/programacao.jpg'})`
                }}  >
                <h1 className='nomeC'>Sobre</h1>
            </Grid>
        </BaseScreen>
    )
}
