import React, { useEffect } from 'react';
import '../App.css';
import { Grid, Typography } from '@material-ui/core';
import { BaseScreen } from "../components";
import { Zoom, Fade, Bounce } from 'react-reveal';

export default ({ history }) => {

    useEffect(_ => { document.title = "HomePage" }, [])

    return (
        <BaseScreen history={history}>
            <Grid container>

                <Grid style={{
                    backgroundImage: "url(" + require('../assets/background1.jpg') + ")",
                    backgroundPosition: 'center',
                    backgroundSize: 'cover',
                    margin: 0
                }} item sm={12} xs={12}>
                    <Zoom>
                        <h1 className='nomeC'>Lucas Pereira de Souza</h1>
                    </Zoom>
                </Grid>

                <Grid style={{
                    backgroundImage: "url(" + require('../assets/background4.jpg') + ")",
                    backgroundPosition: 'center',
                    backgroundSize: 'cover'
                }} item sm={12} xs={12}>
                    <Fade right>
                        <h1 className='nomeC'>Apresentação</h1>
                        <Bounce>
                            <Typography className='nomeC'>
                                Criando sites com Reactjs e utilizando o node
                </Typography>
                        </Bounce>
                    </Fade>
                </Grid>

                <Grid style={{
                    backgroundImage: "url(" + require('../assets/background2.jpeg') + ")",
                    backgroundPosition: 'center',
                    backgroundSize: 'cover'
                }} item sm={12} xs={12}>
                    <Fade left>
                        <h1 className='nomeC'>Conhecimentos Front-End</h1>
                        <Bounce>
                            <Typography className='nomeC'>
                                Reactjs, React Native, Node
                </Typography>
                        </Bounce>
                    </Fade>
                </Grid>

                <Grid style={{
                    backgroundImage: "url(" + require('../assets/background5.jpg') + ")",
                    backgroundPosition: 'center',
                    backgroundSize: 'cover'
                }} item sm={12} xs={12}>
                    <Fade right>
                        <h1 className='nomeC'>Conhecimentos Back-End</h1>
                        <Bounce>
                            <Typography className='nomeC'>
                                Adonisjs, Node, Parse Server
                </Typography>
                        </Bounce>
                    </Fade>
                </Grid>


                <Grid style={{
                    backgroundImage: "url(" + require('../assets/background3.jpeg') + ")",
                    backgroundPosition: 'center',
                    backgroundSize: 'cover'
                }} item sm={12} xs={12}>
                    <Fade left>
                        <h1 className='nomeC'>Conhecimentos Banco de Dados</h1>
                        <Bounce>
                            <Typography className='nomeC'>
                                Mysql, MongoDB
                </Typography>
                        </Bounce>
                    </Fade>
                </Grid>

            </Grid>
        </BaseScreen>
    )
}