import React, { useEffect } from 'react';
import '../App.css';
import { Grid, Typography } from '@material-ui/core';
import { FaFacebook, FaTwitter, FaLinkedin, FaBlogger } from "react-icons/fa";
import { BaseScreen } from "../components";
import { Zoom, Fade, Roll } from 'react-reveal';

export default ({ history }) => {
    useEffect(_ => { document.title = "Contato" }, [])
    return (

        <BaseScreen history={history}>
            <Grid item sm={12} xs={12} className='fundoContato'>
                <Zoom>
                    <h1 className='nomeC'>Contato</h1>
                </Zoom>

                <Fade>
                    <Typography className='nomeC'>
                        Entre em contato comigo
               </Typography>
                </Fade>
            </Grid>


            <Grid item sm={12} xs={12} className='fundoEspaco'>
                <Roll>
                    <h2 className='nomeC'>Dados</h2>
                </Roll>
                <Fade right>

                    <Typography className='nomeC'>
                        Enderço Rua Costa Barros 2363
              </Typography>

                </Fade>
                <Fade left>

                    <Typography className='nomeC'>
                        Telefone: (11) 99222-4891
             </Typography>
                </Fade>
                <Fade right>

                    <Typography className='nomeC'>
                        E-mail: lucaspereiradesouzat@gmail.com
                </Typography>
                </Fade>
            </Grid>

            <Grid item sm={12} xs={12} className='fundoEspaco'>
                <FaFacebook color='white' />
                <FaLinkedin color='white' />
                <FaTwitter color='white' />
                <FaBlogger color='white' />
            </Grid>


        </BaseScreen >
    )
}