import React, { useEffect } from 'react';
import '../App.css';
import { Grid, Typography } from '@material-ui/core';
import { BaseScreen } from "../components";
import { Zoom, Fade } from 'react-reveal';

export default ({ history }) => {
    let projects = require('../assets/projects.json');
    useEffect(_ => { document.title = "Portifolio" }, [])

    return (

        <BaseScreen history={history}>
            <Grid container className='fundoPortifolio'>
                <Grid className='backportifolio' item sm={12} xs={12}>
                    <Fade up>
                        <h1 className='nomeC'>Portifolio</h1>
                        <Zoom >
                            <Typography className='nomeC'>
                                Um Portifolio para mostrar um pouco sobre meus projetos realizados
                               </Typography>
                        </Zoom>
                    </Fade>
                </Grid>

                {
                    projects.map(value => (

                        <Grid item key={value.id} sm={5} xs={12}
                            onClick={_ =>
                                history.push('/Portifolio/' + value.id, value)
                            }
                            className=' '
                            style={{
                                margin: 25,
                                width: '100%',
                                height: 500,
                                backgroundImage: "url(" + require('../assets/' + value.image + value.showimage) + ")",
                            }}>

                            <Fade  {...Number(value.id) % 2 ? 'right' : 'left'}>
                                <Fade right>
                                    <h1 className='redLetters' >{value.title}</h1>
                                </Fade>

                                <Fade left>
                                    <Typography className='redLetters'>
                                        {value.description}
                                    </Typography>
                                </Fade>

                            </Fade>
                        </Grid>

                    ))
                }

            </Grid>
        </BaseScreen>
    )
}