import React from 'react';
import '../App.css';
import Header from "./Header";
import Grid from '@material-ui/core/Grid';

export default ({ history, children }) => {
    return (
        <div className='fundoTelas'>
            <Header history={history} />
            <Grid container className='fundoTelas'  >
                {children}
            </Grid>
        </div>
    )
}